<template>
	<div class="create">
		<!-- <mt-header title="" class="seeInfo">
			<router-link to="/" slot="left">
				<mt-button icon="back"></mt-button>
			</router-link> -->
			<!-- <router-link to="/editInfo" slot="right" class="edit"></router-link> -->
		<!-- </mt-header> -->
		<div class="inform-photo">
			<img :src="informateList.wx_avatar" slot="center">
		</div>
		<table class="informate-tab" v-cloak>
			<tr >
				<td></td>
				<td>{{informateList.truename}}</td>
			</tr>
			<tr>
				<td></td>
				<td>{{informateList.sex | sexFilter}}</td>
			</tr>
			<tr>
				<td></td>
				<td>{{informateList.age}}</td>
			</tr>
			<tr>
				<td></td>
				<td>{{informateList.nation}}</td>
			</tr>
			<tr>
				<td></td>
				<td>{{informateList.id_type | id_typeFilter}}</td>
			</tr>
			<tr>
				<td></td>
				<td>{{informateList.id_code}}</td>
			</tr>
			<tr>
				<td></td>
				<td>{{informateList.blood_type | blood_typeFilter}}</td>
			</tr>
			<tr>
				<td></td>
				<td>{{informateList.children}}</td>
			</tr>
			<tr>
				<td></td>
				<td>{{informateList.hobby_list}}</td>
			</tr>
			<tr>
				<td></td>
				<td>{{informateList.speciality_list}}</td>
			</tr>
			<tr>
				<td></td>
				<td>{{informateList.education | educationFilter}}</td>
			</tr>
			<tr>
				<td></td>
				<td>{{informateList.job_list}}</td>
			</tr>
			<tr>
				<td></td>
				<td>{{informateList.duty}}</td>
			</tr>
			<tr>
				<td></td>
				<td>{{informateList.skill_level_list}}</td>
			</tr>
		</table>
		<div class="next save">
			<mt-button class="next-btn" @click="editClick"></mt-button>
		</div>
	</div>
</template>
<script>
	export default {
		data (){
			return {
				informateList:{}
			}
		},
		created (){
			this.informateList = JSON.parse(window.localStorage.memberList);

		},
		filters: {
			sexFilter:function (input) {
				switch(input){
					case 1: return '';
					case 0: return '';
				}
			},
			id_typeFilter:function (input) {
				switch(input){
					case 0: return '';
					case 1: return '';
					case 2: return '';
					case 3: return '';
				}
			},
			blood_typeFilter:function (input) {
				switch(input){
					case 0: return 'A';
					case 1: return 'B';
					case 2: return 'AB';
					case 3: return 'O';
					case 4: return '';
				}
			},
			educationFilter:function (input) {
				switch(input){
					case 0: return '';
					case 1: return '';
					case 2: return '';
					case 3: return '';
					case 4: return '';
					case 5: return '';
				}
			}
		},
		methods: {
			editClick (){
				window.localStorage.informateList = JSON.stringify(this.informateList);
				this.$router.push('/editInfo');
			}
		}
	}
</script>
<style>
	.inform-photo{
		width: 65px;
		/*height: 65px;*/
		margin: 0 auto;
		margin-top: 20px;
		overflow: hidden;
	}
	.inform-photo img{
		width: 65px;
		height: 65px;
		border-radius: 50%;
		background-color: #f0f0f0;
	}
	.informate-tab{
		margin: 10px 20px;
	}
	.informate-tab tr{
		line-height: 40px;
	}
	.informate-tab tr td{
		padding-right: 20px;
		padding-left: 20px;
		font-size: 16px;
	}
	.seeInfo .mintui{
		font-size: 20px;
	}
	.seeInfo .edit{
		text-decoration: none;
		font-size: 16px;
	}
</style>



